import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(Vuex, VueAxios, axios)

const state = {
	//
	hotFilmData: "",
	//
	futureFilmData1: "",
	futureFilmData2: "",
	//ID
	cityName: "",
	cityId: "",
	//ID
	DatailsCityDataID: "",
	//
	DatailsCityData: "",
	//
	PerformersData: "",
	//
	stillData: "",
	//
	commentData: "",
}

const getters = {
	//8
	hotFilmEight() {
		return state.hotFilmData.slice(0,8)
	},
	//8
	futureFilmEight() {
		return state.futureFilmData1.slice(0,8)
	},
}

const mutations = {
	hotFilm(state, res){
		state.hotFilmData = res.ms
	},
	futureFilm(state, res) {
		state.futureFilmData1 = res.moviecomings
		state.futureFilmData2 = res.attention
	},
	//ID
	SelectCity(state, res) {
		state.cityName = res.cityName
		state.cityId = res.cityId
	},
	//ID
	addDatailsDataId(state, res) {
		state.DatailsCityDataID = res
	},
	//
	addDatailsData(state, res) {
		state.DatailsCityData = res
	},
	//
	PerformersData(state, res) {
		state.PerformersData = res
	},
	//
	stillData(state, res) {
		state.stillData = res
	},
	//
	comment(state, res) {
		state.commentData = res
	}
}

const actions = {
	//
	hot(context) {
		let url
		if(state.cityId) {
			url = "/api/Showtime/LocationMovies.api?locationId="+ state.cityId
		} else {
			url = "/api/Showtime/LocationMovies.api?locationId=365"
		}
		axios.get(url).then(
			response => {
				context.commit('hotFilm', response.data)
			}
		)
	},
	//
	future(context) {
		let url
		if(state.cityId) {
			url = "/api/Movie/MovieComingNew.api?locationId="+ state.cityId
		} else {
			url = "/api/Movie/MovieComingNew.api?locationId=365"
		}
		axios.get(url).then(
			response => {
				context.commit('futureFilm', response.data)
			}
		)
	},
	//ID
	SelectCity(context, payload) {
		context.commit('SelectCity', payload)
	},
	//ID
	DetailsCityID(context, payload) {
		context.commit('addDatailsDataId', payload)
	},
	// 
	DetailsCity(context) {
		let url = "/api/movie/detail.api?locationId="+ state.cityId +"&movieId=" + state.DatailsCityDataID
		axios.get(url).then(
			response => {
				context.commit('addDatailsData', response.data)
			}
		)
	},
	//
	PerformersData(context) {
		let url = "/api/Movie/MovieCreditsWithTypes.api?movieId=" + state.DatailsCityDataID
		axios.get(url).then(
			response => {
				context.commit('PerformersData', response.data.types)
			}
		)
		
	},
	//
	stillData(context) {
		let url = "/api/Movie/ImageAll.api?movieId=" + state.DatailsCityDataID
		axios.get(url).then(
			response => {
				context.commit('stillData', response.data.images)
			}
		)
	},
	//
	comment(context) {
		console.log(state.DatailsCityDataID)
		let url = "/ticket-api/movie/hotComment.api?movieId=" + state.DatailsCityDataID
		axios.get(url).then(
			response => {
				context.commit("comment", response.data.data)
			}
		)
	}
}


const store = new Vuex.Store({
	state,
	getters,
	mutations,
  actions
}) 

export default store